html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: bold;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.moyo-logo {
  width: 40px;
  height: 40px;
}

.nav {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-list :first-child {
  margin-left: 0;
}

.nav-item {
  text-transform: uppercase;
  margin-left: 20px;
}

.nav-link {
  color: #000;
  text-decoration: none;
  position: relative;
}

.nav-link:hover, .nav-link.is-active {
  color: #00acdc;
}

.nav-link:after {
  content: "";
  transform-origin: bottom;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: -22.5px;
  left: 0;
  transform: scaleX(0);
}

.nav-link:hover:after, .nav-link.is-active:after {
  transform: scaleX(1);
}
